import React, { useEffect } from "react";
import "./Profile_photo.css";
import { useState } from "react";
import * as axios from "axios";
import { NavLink } from "react-router-dom";
import { set } from "mongoose";

const Profile_photo = (props) => {
  let [selectedFile, setSelectedFile] = useState(null);
  let [src, setSrc] = useState("");
  let [visCh, setVisCh] = useState(true)

  let fileSelectorHandler = (event) => {
    setSelectedFile(event.target.files[0]);
  };

  let [uId, setId] = useState('') 
    async function huy(){
        const file = await axios.get(
            "https://dry-headland-66248.herokuapp.com/api/upload"
          );
          console.log(file)
          let newFile = file.data._id;
          setId(newFile)
         
    }
 
   
  function fff(){
    if (visCh == false){
      setVisCh(true)
    }
    if(visCh == true){
      setVisCh(false)
    }
   
  }
  function text(e){
    e.currentTarget.addEventListener('change', function(){
      const file = this.files[0]
      if (file){
        const reader = new FileReader()
        reader.addEventListener('load', function(){
          console.log(this.result)
          setSrc(this.result)
          
        })
        reader.readAsDataURL(file)
      }
    })
  }


  async function fds(e) {
    const file = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/upload`
    );
    let newFile = file.data.data;
    if (newFile == undefined){
      setSrc('https://vk.com/images/camera_100.png?ava=1')
    }
    else{ setSrc("data:image/jpeg;base64," + newFile);}
  }
  useEffect(() =>{fds(); huy()}, [])

  return (
    <div  className="profile_photo">
      <img onClick ={fff} src={src || 'https://vk.com/images/camera_100.png?ava=1'} alt="" className="profile_photo_img" />

      <div className="flex_profile_photo" >
       {!visCh && <form 
          className="acd"
          action="https://dry-headland-66248.herokuapp.com/api/upload"
          method="POST"
          encType="multipart/form-data"
        >
          <input  onChange={fileSelectorHandler} type="file" name="file" />
         
          <button>Загрузите фотогрфаию</button>
        </form>}
        <NavLink className="per_to_edit" to="edit">
          <button className="bt1_photo">Редактировать</button>
        </NavLink>
        <button className="bt2_photo">...</button>
        
      </div>
    </div>
  );
};
export default Profile_photo;
