import React, { useContext, useEffect } from "react";
import "./Header_about_me.css";
import { useState } from "react";
import * as axios from 'axios'
import { AuthContext } from "../../../context/Auth-contex";

const Header_about_me = () => {
  let [src, setSrc] = useState('')
  async function fds(e) {
    const file = await axios.get(
      "https://dry-headland-66248.herokuapp.com/api/upload"
    );
    let newFile = file.data.data;
    if (newFile == undefined){
      setSrc('https://vk.com/images/camera_100.png?ava=1')
    }
    else{ setSrc("data:image/jpeg;base64," + newFile);}
  }
  useEffect(() =>{fds()}, [])
  const auth = useContext(AuthContext);
  const logoutHandler = event=> {
    event.preventDefault()
    auth.logout()
  };
  let [log, setLog] = useState(true);
  function revealLog() {
    if (log == false) {
      setLog(true);
    }
    if (log == true) {
      setLog(false);
    }
  }
  return (
    <div className="header_about_me">
      <div onClick={revealLog} className="header_aboutMe_flex">
        <h1>Никита</h1>
        <img
          className="me"
          src={src}
          alt=""
        />
        <svg
          className="arrow"
          width="12px"
          height="16px"
          viewBox="0 0 12 16"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          xlink="http://www.w3.org/1999/xlink"
        >
          <title>chevron_16</title>
          <desc>Created with Sketch.</desc>
          <defs></defs>
          <g
            id="Page-2"
            stroke="none"
            stroke-width="1"
            fill="none"
            fill-rule="evenodd"
          >
            <g id="chevron_16">
              <rect id="Bounds" x="0" y="0" width="12" height="16"></rect>
              <path
                d="M7.2272139,8 L3.86361,4.6363961 C3.51213814,4.28492424 3.51213814,3.71507576 3.86361,3.3636039 C4.21508186,3.01213203 4.78493034,3.01213203 5.13640221,3.3636039 L9.13640221,7.3636039 C9.48787407,7.71507576 9.48787407,8.28492424 9.13640221,8.6363961 L5.13640221,12.6363961 C4.78493034,12.987868 4.21508186,12.987868 3.86361,12.6363961 C3.51213814,12.2849242 3.51213814,11.7150758 3.86361,11.3636039 L7.2272139,8 Z"
                id="Mask"
                fill="#ffffff"
                fill-rule="nonzero"
              ></path>
            </g>
          </g>
        </svg>
      </div>
      {(!log && (
        <div className="log_logout_header">
          <div className="exit_log_header">Настройки</div>
          <div className="exit_log_header">Помощь</div>
          <div className="exit_log_header" onClick ={logoutHandler}>Выйти</div>
        </div>
      )) || <></>}
    </div>
  );
};

export default Header_about_me;
